<template>
	<view>
		<view class="function-body">
			<Tabs @returnIndex="changeFunIndex" :title="functionTitle" :functionIndex="functionIndex"></Tabs>
			
			<view class="title">
				<view class="title-sort" @tap="showSpringBox=(showSpringBox+1)%2">
					<text class="springText">排序</text>
					<text v-if="showSpringBox==false" class="springBoxDown">
						<svg xmlns="http://www.w3.org/2000/svg" width="10" height="6" viewBox="0 0 10 6">
							<path id="多边形_10" data-name="多边形 10" d="M5,0l5,6H0Z" transform="translate(10 6) rotate(180)" fill="#4F73A5"/>
						</svg>
					</text>
					<text v-else class="springBoxUp">
						<svg xmlns="http://www.w3.org/2000/svg" width="10" height="6" viewBox="0 0 10 6">
						  <path id="多边形_10" data-name="多边形 10" d="M5,0l5,6H0Z" fill="#4f73a5"/>
						</svg>
					</text>
				</view>
				
				<view class="arrivalNavigation" v-show="showSpringBox">
					<view class="sideNavigation">
						<view class="navigatorStyle" @tap="">
							<text>价格</text>
							<text class="springBoxUpDown">
								<svg xmlns="http://www.w3.org/2000/svg" width="10" height="25" viewBox="0 0 10 25">
									<path id="多边形_10" data-name="多边形 10" d="M5,0l5,6H0Z" transform="translate(0,8)" fill="#4f73a5"/>
									<path id="多边形_10" data-name="多边形 10" d="M5,0l5,6H0Z" transform="translate(10,25) rotate(180)" fill="#4F73A5"/>
								</svg>
							</text>
						</view>
						<view class="liBottomBorder"></view>
						<view class="navigatorStyle" @tap="">
							<text>剩余时间</text>
							<text class="springBoxUpDown">
								<svg xmlns="http://www.w3.org/2000/svg" width="10" height="25" viewBox="0 0 10 25">
									<path id="多边形_10" data-name="多边形 10" d="M5,0l5,6H0Z" transform="translate(0,8)" fill="#4f73a5"/>
									<path id="多边形_10" data-name="多边形 10" d="M5,0l5,6H0Z" transform="translate(10,25) rotate(180)" fill="#4F73A5"/>
								</svg>
							</text>
						</view>
					</view>
				</view>
			</view>
			
			
			<view class="content">
				<view class="goods-item" v-for="(item,index1) in currentDetail" :key="item.id" @tap="toClassify(item.id)">
				    <view class="item-img">
						<image class="item-pic" mode="scaleToFill" :src="item.introImage"></image>
						
					</view>
				    <view class="item-block">
				        <view class="item-detail">
				            <text class="item-title">name</text>
				            <text class="item-date">预计剩余时间</text>
				            <text class="item-payment">当前最高价格 ￥</text>
				        </view>
						<view class="item-tags" v-for="(item,index) in 3" :key="index">
							<text>tag</text>
						</view>
				    </view>
					
				</view>
			</view>
			
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				functionTitle:["商品","拍卖品"],
				functionIndex:0,
				currentDetail:["",""],
				showSpringBox:false,
				sortPriceUp:false,
			}
		},
		methods: {
			//切换tab选项卡的标题
			changeFunIndex(index){
				this.functionIndex=index
				console.log(index)
			},
			//根据functionIndex判断跳转页面
			toClassify(id){
				if(this.functionIndex===0){
					uni.navigateTo({
						url:"../living/living?id="+id,
					})
				}else if(this.functionIndex===1){
					uni.navigateTo({
						url:"../artSalon/artSalon?id="+id
					})
				}else{
					uni.navigateTo({
						url:"../course-template/course-template?id="+id
					})
				}
			},
		},
		
		/**
		 * 生命周期函数--监听页面加载
		 */
		onLoad: function(option) {
			this.functionIndex = Number(option.type)
			console.log(this.functionIndex)
		},
		/**
		 * 生命周期函数--监听页面初次渲染完成
		 */
		onReady: function() {},
		/**
		 * 生命周期函数--监听页面显示
		 */
		onShow: function() {},
		/**
		 * 生命周期函数--监听页面隐藏
		 */
		onHide: function() {},
		/**
		 * 生命周期函数--监听页面卸载
		 */
		onUnload: function() {
		},
		/**
		 * 页面相关事件处理函数--监听用户下拉动作
		 */
		onPullDownRefresh: function() {},
		/**
		 * 页面上拉触底事件的处理函数
		 */
		onReachBottom: function() {},
		/**
		 * 用户点击右上角分享
		 */
		onShareAppMessage: function() {},
	}
	
</script>

<style>
	@import url("./mycollections.css");
</style>
